@import url('https://fonts.googleapis.com/css2?family=Nanum+Brush+Script&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #f4f4f4;
}




/* font-family: 'Poppins', sans-serif; */
/* font-family: 'Playfair Display', serif; */

/* Navbar Css */


.navbar {
  font-family: 'Poppins', sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px gray;
  border-bottom: solid gray !important;
  background-color: #131921;
  color: #ffffff;
}

.navbar-nav .nav-link {
  color: #ffffff;
  font-weight: 500;
  padding: 0.5rem 1rem;
}

.navbar-nav .nav-link:hover {
  color: #ff9900;
}

.navbar-form {
  display: flex;
  align-items: center;
}

.navbar-form .form-control {
  margin-right: 10px;
  border: none;
  border-radius: 4px;
  padding: 5px 10px;
}

.navbar-form .btn-outline-success {
  background-color: #ff9900;
  border: none;
  color: #232f3e;
  font-weight: bold;
}

.navbar-nav-right .nav-link {
  color: #ffffff;
  font-weight: bold;
  padding: 0.5rem 1rem;
}

@media (max-width: 768px) {

  /* Center-align the brand logo */
  .navbar-brand {
    text-align: center;
    width: 100%;
  }

  /* Center-align the search bar */
  .navbar-form {
    justify-content: center;
  }

  .navbar-nav-right {
    justify-content: center;
  }




  .navbar-nav .nav-link {
    padding: 0.5rem 0;
    color: #ffffff;
  }
}

.home-link {
  color: #ffffff !important;

}

.admin-link {
  color: #ffffff !important;
}

.admin-link:hover {
  color: #ff9900 !important;
}


.navbar .navbar-nav .nav-item .home-link:hover {
  color: #ff9900 !important;
  /* Use !important as a last resort */
}

.cart-link {
  font-size: 16px;
  margin-top: -5px;
}

.navbar-brand {
  font-size: 24px;
  font-weight: 500;
  color: #ffffff;
}










.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}

.row {
  margin-top: 70px;
}

.col-md-4 {
  align-items: end;
}

.about-page {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.about-page p {
  font-size: 20px;
  font-weight: 100;
  font-family: 'roboto', serif;

}

.policy {
  font-family: Arial, sans-serif;
  margin: 20px;
  line-height: 1.6;
  text-align: center;
}

.contactus {
  margin: 0;
  padding: 0;
  height: 400px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* 
--------------------------------- Register Page */

.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}

.product-link {
  text-decoration: none !important;
  color: black !important;
}