.footer {
    background-color: #333;
    /* Background color for the footer */
    color: #fff;
    /* Text color for the footer */
    padding: 20px 0;
    /* Add some padding to the top and bottom */
    text-align: center;
}

/* Style for the copyright text */
.footer h4 {
    font-size: 16px;
}

/* Style for the links in the footer */
.footer a {
    color: #fff;
    /* Text color for the links */
    text-decoration: none;
    /* Remove underlines from links */
    margin: 0 10px;
    /* Add some space between links */
    font-size: 14px;
}

/* Hover effect for links */
.footer a:hover {
    text-decoration: underline;
    /* Add an underline on hover */
}