.banner-img {
    margin-top: 60px;
}

.home-page h1 {
    color: rgb(0, 0, 0);
    font-family: "Playfair Display", serif;
}

.home-page {
    display: flex;
    flex-wrap: wrap;
}

.home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
}



.home-page .card-text {
    color: rgb(3, 0, 0);
}

.home-page .card img:hover {
    transform: scale(0.9);
}

.home-page .card-img-top {
    height: 300px !important;
}

/* ==================
  filters
  ============== */
.filters h4 {
    margin-top: 100px;
    color: rgb(33, 1, 1) !important;
    border-bottom: 1px solid rgba(2, 0, 0, 0.943);
}

.ant-checkbox-wrapper:first-of-type {
    margin-left: 8px;
}

.ant-checkbox-wrapper {
    margin: 2px;
}

.ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
}

.card-body {
    text-align: center;
    font-size: 500;
}